import React from "react";
import "./forms.css";
import Result from "./result";

class Forms extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      city: "",
      result: []
    };
  }

  handleChange = (e) => {
    e.preventDefault();
    this.setState({ [e.target.name]: [e.target.value] });
  };
  searchWeather = (e) => {
    e.preventDefault();
    console.log(this.state.city);
    fetch(
      "https://community-open-weather-map.p.rapidapi.com/weather?q=" +
        this.state.city +
        "&lat=0&lon=0&id=2172797&lang=null&units=%22metric%22%20or%20%22imperial%22&mode=xml%2C%20html",
      {
        method: "GET",
        headers: {
          "x-rapidapi-key":
            "c8cb6b8cccmshc02b57f0b5a8c98p1516cdjsnb64d72a5ad33",
          "x-rapidapi-host": "community-open-weather-map.p.rapidapi.com"
        }
      }
    )
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        console.log(data);
        this.setState({ result: [data] });
      })
      .catch((err) => {
        console.error(err);
      });
  };
  render() {
    const resultJSX = this.state.result.map((item) => (
      <Result tempMin={item.main.temp_min} tempMax={item.main.temp_max} />
    ));
    return (
      <div id="all">
        <div id="forms">
          <form>
            <input
              type="text"
              placeholder="City Name"
              name="city"
              value={this.state.city}
              onChange={this.handleChange}
            />
            <button onClick={this.searchWeather}>Search Weather</button>
          </form>
        </div>
        {resultJSX}
      </div>
    );
  }
}

export default Forms;
